body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #282c34;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.controls {
  /*display: flex;
  align-items:stretch;*/
  /*background: white;*/
  margin: 20px auto 40px auto;
  width: 100%;
}
.TileInner {
  height: 100%; }

.Tile {
  height: 100%;
  box-sizing: border-box;
  float: left;
  border: 5px solid #212121; }
  .Tile.playing {
    padding: 3px; }

.Row {
  overflow: hidden;
  margin: 0 auto; }

.RowOuter {
  position: relative; }

.rowButton {
  display: inline;
  opacity: 0;
  width: 40px;
  height: 100%;
  position: absolute;
  top: 0px;
  background: #212121;
  border-radius: 10px;
  color: white;
  font-family: Helvetica;
  font-weight: 500px;
  font-size: 20px;
  line-height: 200px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .rowButton.add {
    left: 805px;
    border-radius: 0px 10px 10px 0px; }
    .rowButton.add.long {
      top: 805px;
      border-radius: 0px 0px 10px 10px; }
  .rowButton.subtract {
    left: -45px;
    border-radius: 10px 0px 0px 10px; }
    .rowButton.subtract.long {
      top: -45px;
      border-radius: 10px 10px 0px 0px; }
  .rowButton.long {
    visibility: visible;
    opacity: 0;
    width: 810px;
    height: 40px;
    left: -5px;
    line-height: 40px;
    display: block; }
  .rowButton:hover {
    color: white;
    opacity: 1;
    cursor: pointer; }

.Board {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 40px auto;
  border: 5px solid #212121;
  background: #212121;
}
